        <h1>Com publicar a lAprn</h1>

        <p>Per tal que un contingut docent aparegui publicat al portal <strong>Aprn</strong>, cal que abans hagi estat dipositat a <a href="https://upcommons.upc.edu/">UPCommons</a>, el portal del coneixement obert de la UPC o a <a href="https://zonavideo.upc.edu/">Zonavdeo</a> si es tracta dun audiovisual.</p>
        <p>El professorat de la UPC pot publicar continguts docents a <a href="https://upcommons.upc.edu/">UPCommons</a> adreant-se a la seva biblioteca, o mitjanant <a href="https://atenea.upc.edu/">Atenea</a>, lentorn virtual daprenentatge de la UPC.</p>


        <h4 class="mb-3">Beneficis de publicar a lAprn&nbsp;</h4>
