        <h4>Com dipositar continguts docents a UPCommons des dAtenea</h4>
        <p>Ja es poden incorporar continguts docents a UPCommons directament des d<a href="https://atenea.upc.edu/">Atenea</a>, lentorn virtual daprenentatge de la UPC.</p>
        <p>El procs que sha definit s molt simple i permet que, de manera rpida, el PDI pugui compartir a UPCommons els arxius que prviament ha incorporat a les seves assignatures.</p>
        <p>Un cop dipositat a UPCommons, els continguts apareixeran tamb al portal <strong>Aprn</strong>, vinculats al  perfil del professorat, aix com al de les assignatures, centres i titulacions en les quals sest utilitzant. </p>
        <p>Laplicaci permet tamb que, des de la prpia assignatura dAtenea, es pugui fer el seguiment de lestat de la seva publicaci.</p>
        <p dir="ltr">&nbsp;</p>

        <h4>Com fer-ho?</h4>
