        </p>
        <ul class="mt-4">
          <li class="ml-5 mb-3" aria-level="1">Pots editar, modificar o ampliar les dades (afegir autores i autors, ) abans de que senvin, juntament amb larxiu a UPCommons.</li>
          <li class="ml-5 mb-3" aria-level="1">Totes les metadades sn revisades i ampliades per part del personal del <a href="https://bibliotecnica.upc.edu/">Servei de Biblioteques, Publicacions i Arxius</a> (SBPA)</li>
